import styled from "styled-components";

export const Container = styled.div`
    font-size:10px;
    width:100%;
    height:50px;
    background:#0d1117;
    position:fixed;
    bottom:0;
    display:flex;
    flex-direction:column;
    border-top:5px solid #20295F;
    align-items:center;
    justify-content:center;
    span{
        color:#FFF;
    }

    a{
        text-decoration:none;
        color:#FFF;
    }
`;