import styled from "styled-components";

export const Container = styled.div`
    width:100%;
    height:70px;
    background:#0d1117;
    display:flex;
    border-bottom:5px solid #20295F;

`;

export const LeftSide = styled.div`
    width:50%;
    height:70px;
    display:flex;
    align-items:center;
    padding-left:10px;

    img{
        width:40px;
        height:35px;
    }

`;

export const RightSide = styled.div`
    width:50%;
    height:70px;
    display:flex;
    align-items:center;
    justify-content:flex-end;

       h1{
           color:#fff;
           padding:3px 7px;
       }

`;