import React from 'react';

import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';

import * as S from './style'

export default function MyModal({ open, data, onCloseModal }) {
    return (
        <Modal open={open} onClose={onCloseModal}>
            <S.Container>
                <S.Title>
                    <h2>{data.country}</h2>
                </S.Title>
                <S.Flag>
                    <img src={data.countryInfo.flag} alt="flag" />
                </S.Flag>
                <S.Content>
                    <S.Item>
                        Active: <S.Number>{data.active}</S.Number>
                    </S.Item>
                    <S.Item>
                        Active Per One Million: {data.activePerOneMillion}
                    </S.Item>

                    <S.Item>
                        Cases: <S.Number>{data.cases}</S.Number>
                    </S.Item>

                    <S.Item>
                        Cases Per One Million:  <S.Number>{data.casesPerOneMillion}</S.Number>
                    </S.Item>

                    <S.Item>
                        Continent: <S.Number>{data.continent}</S.Number>
                    </S.Item>

                    <S.Item>
                        Country: <S.Number>{data.country}</S.Number>
                    </S.Item>

                    <S.Item>
                        Critical:<S.Number> {data.critical}</S.Number>
                    </S.Item>

                    <S.Item>
                        Critical Per One Million: <S.Number>{data.criticalPerOneMillion}</S.Number>
                    </S.Item>

                    <S.Item>
                        Deaths: <S.Number>{data.deaths}</S.Number>
                    </S.Item>

                    <S.Item>
                        Deaths Per One Million: <S.Number>{data.deathsPerOneMillion}</S.Number>
                    </S.Item>

                    <S.Item>
                        One Case Per People: <S.Number>{data.oneCasePerPeople}</S.Number>
                    </S.Item>

                    <S.Item>
                        One Test Per People: <S.Number>{data.oneTestPerPeople}</S.Number>
                    </S.Item>

                    <S.Item>
                        Population: <S.Number>{data.population}</S.Number>
                    </S.Item>

                    <S.Item>
                        Recovered: <S.Number>{data.recovered}</S.Number>
                    </S.Item>

                    <S.Item>
                        Recovered Per One Million: <S.Number>{data.recoveredPerOneMillion}</S.Number>
                    </S.Item>

                    <S.Item>
                        Tests: <S.Number>{data.tests}</S.Number>
                    </S.Item>

                    <S.Item>
                        Tests Per One Million: <S.Number>{data.testsPerOneMillion}</S.Number>
                    </S.Item>

                    <S.Item>
                        TodayCases: <S.Number>{data.todayCases}</S.Number>
                    </S.Item>

                    <S.Item>
                        Today Deaths: <S.Number>{data.todayDeaths}</S.Number>
                    </S.Item>

                    <S.Item>
                        Today Recovered:  <S.Number>{data.todayRecovered}</S.Number>
                    </S.Item>
                </S.Content >
            </S.Container >

        </Modal >
    )
}