import styled from 'styled-components'

export const Container = styled.div`
    width:450px;
    height:450px;
    margin:25px;
    display:flex;
    flex-direction:column;
    /* align-items:center; */
`

export const Title = styled.div`
    h2{
        color:#2c3e50;
        text-align:center;
    }


`

export const Content = styled.div`
    display:flex;
    flex-direction:column-reverse;
`
export const Item = styled.div`
    margin-top:10px;
    display:flex;
    flex-direction:row;
    font-weight:bold;
    margin-bottom:10px;
`

export const Number = styled.div`
    font-weight:bold;
    margin-left:10px;
    color:#c0392b;
`

export const Flag = styled.div`
    display:flex;
    justify-content:center;
    margin-top:10px;
    margin-bottom:10px;

    img{
        border-width:2px;
        width:200px;
        height:110px;
        border-radius:12px;
        border-color:#34495e;
        border-style:solid;
    }

`