import styled from "styled-components";

export const Container = styled.div`
width:100%;
    input{
        height:30px;
        width:97%;
        margin:10px;
        padding:5px;
    
        border-width:1px;
        border:none;
        border-bottom:1px solid #2c3e50; 

        font-weight:bold;
        font-size:18px;
        color:#130f40;

        &:focus{
            outline:0px;
            outline-offset:0px;
            border-bottom:2px solid #2c3e50; 
        }
    }

   

`;
export const Content = styled.div`
 
    width:100%;
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
    margin-bottom:70px;
    
    
    
`;

