import React, { useState } from 'react';
import MyModal from '../MyModal';
import * as S from './style'

export default function CardCountry({ data }) {

    const [isOpen, setOpenModal] = useState(false);

    function openCloseModal(opened) {
        if (opened) {
            setOpenModal(false)
        } else {
            setOpenModal(true)
        }
    }

    return (
        <S.Container onClick={() => openCloseModal(isOpen)}>
            <MyModal
                onCloseModal={() => openCloseModal(isOpen)}
                open={isOpen}
                data={data}
            />
            <S.Content>

                <S.Title>
                    <span>{data.country}</span>
                </S.Title>
                <S.Flag>
                    <img src={data.countryInfo.flag} alt="flag" />
                </S.Flag>
            </S.Content>
        </S.Container>

    )
}