import styled from 'styled-components';

export const Container = styled.div`
    width:230px;
    height:150px;
    box-shadow: -3px 1px 13px -2px rgba(0,0,0,0.75);
    border-radius:10px;
    display:flex;
    align-items:center;
    justify-content:space-between;
    flex-direction:column ;
    border-width:2px;
    border-style:solid;
    border-color:#fff;
    margin:15px;

    &:hover{
        opacity:0.9;
        cursor: pointer;
        border-width:2px;
        border-color:darkblue;
        border-style:solid;
        transition:all 0.8s ease;
    }
`

export const Title = styled.div`
    display:flex;
    text-align:center;
    margin-top:10px;
    font-weight:bold;
    font-size:16px;
`

export const Flag = styled.div`
    img{
        width:200px;
        height:110px;
        border-radius:12px;
        border-color:#34495e;
        border-style:solid;
        border-width:1.5px;
    }

`

export const Content = styled.div`
display:flex;
    align-items:center;
    justify-content:space-between;
    flex-direction:column ;
`