import React from "react";
import * as S from './style'
import Corona from '../../img/coronavirus.png'

function Header() {
    return (
        <S.Container>
            <S.LeftSide>
                <img src={Corona} alt="CoronaImage" />
            </S.LeftSide>
            <S.RightSide>
                <h1>Corona Virus Status</h1>
            </S.RightSide>
        </S.Container>
    );


}

export default Header;