import React, { useEffect, useState } from 'react';
import axios from 'axios';
import ContentLoader from 'react-content-loader';

import * as S from './styles'


import Header from "../../components/Header";
import Footer from '../../components/Footer';
import CardCountry from '../../components/CardCountry'


function Home() {

    const [totalGlobal, setData] = useState();

    async function getTotalCountries() {
        await axios.get('https://corona.lmao.ninja/v2/countries')
            .then(resp => {
                setData(resp.data);
            })
    }

    function filter(value) {
        if (value) {
            let result = totalGlobal.filter(filtro => filtro.country.toLowerCase().includes(value))
            setData(result);
        } else {
            getTotalCountries();
        }
    }

    useEffect(() => {
        getTotalCountries();
    }, []);


    return (
        <S.Container>
            <Header />
            <input
                onChange={(e) => filter(e.target.value)}
                placeholder="Search a Country Here..."
            />
            <S.Content>
                {
                    totalGlobal ?
                        totalGlobal.map((data, idx) => {
                            return (
                                <CardCountry
                                    data={data}
                                    key={idx}
                                />
                            )
                        })
                        :
                        <ContentLoader viewBox="0 0 380 70">
                            <rect x="0" y="0" rx="5" ry="5" width="70" height="70" />
                        </ContentLoader>
                }
            </S.Content>
            <Footer />
        </S.Container>
    )
}

export default Home;